<script>
export default {
    name: 'APetEcuRecord',
    props: {
        record: Object
    },
    // mounted: {

    // },
    data() {
        return {
            imgUrl: require("../assets/img/ygwc.png"),
            headerSrc: require("../assets/img/head.jpg"),
            carRgb:[143,117,197],
            petRgb:[225,54,54],
            ecuRgb:[237,196,132]
        }
    },
    methods: {
        colorToRgb: function(color16,opicity) {
            // 16进制颜色值的正则
            var reg = /^#([0-9a-fA-f]{3}|[0-9a-fA-f]{6})$/;
            // 把颜色值变成小写
            var color = color16.toLowerCase();
            if (reg.test(color)) {
                // 如果只有三位的值，需变成六位，如：#fff => #ffffff
                // if (color.length === 4) {
                //     var colorNew = "#";
                //     for (var i = 1; i < 4; i += 1) {
                //         colorNew += color.slice(i, i + 1).concat(color.slice(i, i + 1));
                //     }
                //     color = colorNew;
                // }
                // 处理六位的颜色值，转为RGB
                var colorChange = [];
                for (var i = 1; i < 7; i += 2) {
                    colorChange.push(parseInt("0x" + color.slice(i, i + 2)));
                }

                return "RGB(" + colorChange.join(",") + ")";
            } else {
                return color;
            }
        }
    }
}
</script>

