<template>
    <div class="a_pet_ecu_record_list">
        
        <div class="list_header">
            <span v-text="typeNew?getToday(trackLabel):trackLabel"></span><span class="count"> · <span v-text="records.length"></span>张</span>
            <span class="hide" v-if="!typeNew" :id="id">.</span>
        </div>
        <div class="list_body">
            <el-row class="row" :gutter="10">
                <el-col class="col" :xs="24" :sm="12" :md="12" :lg="8" :xl="6"
                    v-for="record in records" 
                    :key="record.recordId">
                    <APetEcuRecord
                        class="list_item" 
                        :record="record">
                    </APetEcuRecord>
                </el-col>
            </el-row>
        </div>
    </div>
</template>

<script>
import APetEcuRecord from './APetEcuRecord.vue';

export default {
    name: 'APetEcuRecordList',
    props: {
        records: Array,
        trackLabel: String,
        typeNew: Boolean,
        id: String
    },
    mounted(){
        this.initToday();
    },
    components: {
        APetEcuRecord
    },
    data() {
        return {
            d1:'',
            d2:'',
            d3:''
        };
    },
    methods: {
        //初始化今天的字符串
        initToday: function(){
            let today = new Date();
            this.d1 = this.getTimeStrByDate(today);
    
            let today2 = new Date();
            today2 = today2.setDate(today2.getDate() - 1);
            this.d2 = this.getTimeStrByDate(new Date(today2));
    
            let today3 = new Date();
            today3 = today3.setDate(today3.getDate() - 2);
            this.d3 = this.getTimeStrByDate(new Date(today3));
        },
    
        getToday: function(day){
            if(this.d1 == day) {
                return '今天';
            } else if(this.d2 == day){
                return '昨天';
            } else if(this.d3 == day){
                return '前天';
            } else {
                return day;
            }
        },
    
        //日期对象获得字符串
        getTimeStrByDate: function(date) {
            var y = date.getFullYear();
            var M = date.getMonth() + 1;
            var d = date.getDate();
            return y + '-' + (M < 10 ? ('0' + M) : M) + '-' + (d < 10 ? ('0' + d) : d);
        },
    }
}
</script>

<style lang="less" scoped>
.a_pet_ecu_record_list {
    .hide {
        height: 0px;
        position: relative;
        top: -3.5rem;
        visibility:hidden;
    }
    .list_header {
        height: 1rem;
        line-height: 1rem;
        padding-left: 0.5rem;
        margin: 1rem 0;
        font-size: 1rem;
        color: @color_grey1;
        .count {
            color: @color_grey2;
        }
    }

    .list_body {

    }
}

</style>
